import {_decorator, Camera, Component, EventKeyboard, input, Input, KeyCode, Vec3} from 'cc';

const {ccclass, property} = _decorator;

@ccclass('Swipe_Camera')
export class Swipe_Camera extends Component {
  @property(Camera) camera: Camera;

  onLoad() {
    this.register_event();
  }

  onDestroy() {
    this.unregister_event();
  }

  register_event() {
    input.on(Input.EventType.KEY_DOWN, this.on_key_down, this);
    input.on(Input.EventType.KEY_UP, this.on_key_up, this);
    input.on(Input.EventType.KEY_PRESSING, this.on_key_pressing, this);
  }

  unregister_event() {
    input.off(Input.EventType.KEY_DOWN, this.on_key_down, this);
    input.off(Input.EventType.KEY_UP, this.on_key_up, this);
    input.off(Input.EventType.KEY_PRESSING, this.on_key_pressing, this);
  }

  on_key_down(event: EventKeyboard) {
    let key_code = event.keyCode;

    let camera_position = this.camera.node.position;

    switch (key_code) {
      case KeyCode.KEY_W:
        this.camera.node.setPosition(camera_position.add(Vec3.UNIT_Y));
        break;
      case KeyCode.KEY_S:
        this.camera.node.setPosition(camera_position.subtract(Vec3.UNIT_Y));
        break;
      case KeyCode.KEY_A:
        this.camera.node.setPosition(camera_position.subtract(Vec3.UNIT_Z));
        break;
      case KeyCode.KEY_D:
        this.camera.node.setPosition(camera_position.add(Vec3.UNIT_Z));
        break;
      case KeyCode.KEY_Q:
        this.camera.node.setPosition(camera_position.subtract(Vec3.UNIT_X));
        break;
      case KeyCode.KEY_E:
        this.camera.node.setPosition(camera_position.add(Vec3.UNIT_X));
        break;
    }
  }

  on_key_up(event: EventKeyboard) {}

  on_key_pressing(event: EventKeyboard) {}
}
